<template>
<div class="cover">
  <div class="running-text">
    <MarqueeText :duration="80">
        {{lyrics}}
    </MarqueeText>
  </div>
  <div class="logo">
    <img src="../assets/loading/loading.gif"/>
  </div>
  <div class="loading"> LOADING </div>
</div>
</template>

<script>
import MarqueeText from 'vue-marquee-text-component'

export default {
  name: "LoadingScreen",
  components: {MarqueeText},
  props: {
    loaded : Boolean
  },
  data() {
    return {
      lyrics: "wi wi wi wi  " +
          "nng fu chngwi jn wn n i de kuwi  " +
          "iy iy  " +
          "ddn s p w jizhung de fngbi i  " +
          "bae bae bae bae  " +
          "dyng b hu rng w kngshu r gu  " +
          "party on night  " +
          "yunling w qngf de zhungti  " +
          "xiol b tng zhungj w de xingp  " +
          "n guy wnmi hu sh w yngs  " +
          "x xi de yunwng sh y n gngch  " +
          "qio xing w de mn yo jn w de chn  " +
          "cng rch do hunghn                  "
    }
  }, mounted() {
    window.addEventListener("load",  async () => {
      if (!this.loaded) {
        setTimeout(()=> {
          this.$emit('update:loaded', true)
        },6000)
      }
    });
  }
}
</script>

<style scoped>
.cover {
  height: 100%;
  width: 100%;
  background: #5318f3;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.running-text {
  font-family: 'Roboto', sans-serif;
  position: absolute;
  top: 5%;
  width: 800px;
  left: calc(50% - 400px);
  color: #00d4d5;
  font-size: 20px;
  letter-spacing: 2px;
}

.logo {
  position: absolute;
  top: 15%;
  left: calc(50% - 125px);
}

.loading {
  width: 300px;
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: calc(50% - 150px);
  color: #00d4d5;
}


</style>
