<template>
  <div class="container-h">
    <div style="height: 100%" class="row">
      <div class="col row s8 music-box">
        <div class="col s12 info">
          <audio style="display: none" id="audio" muted :src="require('../../'+ SongMusic)"></audio>
          <div class="album-cover s12">
            <img class="photo"  :src="require('../../'+ SongThumbnail)"/>
          </div>
          <div class="play-title">
            <h5  class="s12">Now playing - {{ SongName }}</h5>
          </div>
        </div>
        <div class="col s12 navigator">
          <div class="left-nav-bar">
            <div class="skip" @click.prevent="previousTrack">
              <svg style="height: 40px;" fill="#000000" version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg"
                   xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24" xml:space="preserve"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                <g id="SVGRepo_iconCarrier"> <g> <path
                    d="M14,5h-2v2h-2v2H8v1H7v4h1v1h2v2h2v2h2v2h6V3h-6 M10,13v-2h2V9h2V7h2V5h2v14h-2v-2h-2v-2h-2v-2H10z"></path>
                  <rect x="4" y="3" width="2" height="18"></rect> </g> </g></svg>
            </div>
          </div>
          <div class="progress-nav-bar">
            <div class="bar">
            </div>
            <div class="break" id="player" @click="playMusic">
              <div :class="{ hidden:!play }">
                <svg style="height: 40px; width: 40px" fill="#000000" height="200px" width="200px" version="1.1"
                     id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
                     viewBox="0 0 24 24" xml:space="preserve"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                  <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                  <g id="SVGRepo_iconCarrier"> <path
                      d="M18,11v-1h-1V9h-2V7h-2V5h-2V3H5v18h6v-1v-1h2v-2h2v-2h2v-1h1v-1h1v-2H18z M13,13v2h-2v2H9v2H7V5h2v2h2v2h2v2h2v2H13z"></path> </g></svg>
              </div>
              <div :class="{ hidden:play }" class="pause">
                <svg style="height: 40px; width: 40px" fill="#000000" height="40px" width="40px" version="1.1"
                     id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
                     viewBox="0 0 24 24" xml:space="preserve"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                  <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                  <g id="SVGRepo_iconCarrier"> <path d="M4,4v16h16V4H4z M18,18H6V6h12V18z"></path> </g></svg>
              </div>
            </div>
          </div>
          <div class="right-nav-bar">
            <div class="skip" @click.prevent="skipTrack">
              <svg style="height: 40px" fill="#000000" version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg"
                   xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 24 24" xml:space="preserve"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                <g id="SVGRepo_iconCarrier"> <g> <path
                    d="M10,3H4v18h6v-2h2v-2h2v-2h2v-1h1v-4h-1V9h-2V7h-2V5h-2 M12,13v2h-2v2H8v2H6V5h2v2h2v2h2v2h2v2H12z"></path>
                  <rect x="18" y="3" width="2" height="18"></rect> </g> </g></svg>
            </div>
            <div :class="{ hidden:mute }" @click.prevent="muteM">
              <svg style="height: 40px; width: 40px" fill="#000000" height="200px" width="200px" version="1.1"
                   id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
                   viewBox="0 0 24 24" xml:space="preserve"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                <g id="SVGRepo_iconCarrier"> <g> <polygon
                    points="16,14 17,14 17,10 16,10 16,9 15,9 15,15 16,15 "></polygon>
                  <path
                      d="M11,3v1h-1v1H9v1H8v1H7v1H6v1H5v1H4v1H3v2h1v1h1v1h1v1h1v1h1v1h1v1h1v1h1v1h2V3H11z M11,16h-1v-1H9v-1H8v-1H7v-2h1v-1h1V9 h1V8h1V16z"></path>
                  <polygon
                      points="19,7 19,6 15,6 15,8 17,8 17,9 18,9 18,15 17,15 17,16 15,16 15,18 19,18 19,17 20,17 20,7 "></polygon> </g> </g></svg>
            </div>
            <div :class="{ hidden:!mute }" @click.prevent="unmuteM">
              <svg style="height: 40px; width: 40px" fill="#000000" height="200px" width="200px" version="1.1"
                   id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"
                   viewBox="0 0 24 24" xml:space="preserve"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                <g id="SVGRepo_iconCarrier"> <g> <path
                    d="M11,3v1h-1v1H9v1H8v1H7v1H6v1H5v1H4v1H3v2h1v1h1v1h1v1h1v1h1v1h1v1h1v1h1v1h2V3H11z M11,16h-1v-1H9v-1H8v-1H7v-2h1v-1h1V9 h1V8h1V16z"></path>
                  <polygon
                      points="19,10 19,11 18,11 18,10 16,10 16,12 17,12 17,13 16,13 16,15 18,15 18,14 19,14 19,15 21,15 21,13 20,13 20,12 21,12 21,10 "></polygon> </g> </g></svg>
            </div>
          </div>
        </div>
      </div>
      <div class="col s4 list-box">
        <div class="border"></div>
        <div class="list">
          <h6 class="title">List</h6>
          <div v-for="(song,i) in SongLists" class="choice" :key="i">
            <h6 @click="chooseSong(i)" :class="{songs :true,chosen:song==SongName}">{{ song }}</h6>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {Data} from '../../music/data'

export default {
  name: "MusicPlayer",
  data() {
    return {
      songs: Data,
      play: true,
      currentSong: 0,
      audio: {},
      totalTime: 0,
      playbar: {},
      minimumHeight: 400,
      minimumWidth: 500,
      mute: false,
    }
  },
  computed: {
    SongLists() {
      return this.songs.map(song => {
        return song.name
      })
    },
    SongName() {
      return this.songs[this.currentSong].name
    },
    SongArtist() {
      return this.songs[this.currentSong].artist
    },
    SongThumbnail() {
      return this.songs[this.currentSong].thumbnail
    },
    SongMusic() {
      return this.songs[this.currentSong].music
    }
  },
  methods: {
    chooseSong(i) {
      this.currentSong = i
      this.audio.src = require('../../' + this.SongMusic)
      this.audio.load()
      this.play = false
      this.audio.play()
    },
    autoPlay() {
      let retry = 0
      while (retry > 5) {
        if (this.audio.readyState >= 2) {
          if (!this.play) {
            this.audio.pause()
            this.play = !this.play
            return
          }
          this.audio.play()
          this.play = !this.play
        }
      }
    },
    muteM() {
      this.audio.volume = 0
      this.mute = true
    },
    unmuteM() {
      this.audio.volume = 0.5
      this.mute = false
    },
    skipTrack() {
      this.currentSong = this.currentSong == this.songs.length - 1 ? 0 : this.currentSong + 1
      this.audio.src = require('../../' + this.SongMusic)
      this.audio.pause()
      this.audio.load()
      this.play = true
    },
    previousTrack() {
      this.currentSong = this.currentSong == 0 ? 0 : this.currentSong - 1
      this.audio.src = require('../../' + this.SongMusic)
      this.audio.pause()
      this.audio.load()
      this.play = true
    },
    playMusic() {
      if (this.audio.readyState >= 2) {
        if (!this.play) {
          this.audio.pause()
          this.play = !this.play
          return
        }
        this.audio.play()
        this.play = !this.play
      }
    }
  },
  mounted() {
    this.audio = document.getElementById('audio')
    this.audio.src = require('../../' + this.SongMusic)
    this.audio.volume = 0.5
    this.playbar = document.getElementById('player')
    this.audio.addEventListener
    this.audio.addEventListener('durationchange', () => {
      this.totalTime = this.audio.duration
    })
    this.audio.addEventListener('timeupdate', () => {
      this.playbar.style.setProperty('left', this.audio.currentTime / this.totalTime * 100 - 5 + "%", 'important')
    })
    this.audio.addEventListener('ended', () => {
      this.skipTrack()
    })
  }
}
</script>

<style scoped>
@import "~materialize-css/dist/css/materialize.css";
.play-title {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.photo {
  max-height: 100%;
  max-width: 100%;
}

.album-cover {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  height: 70%;
}

.container-h {
  background: #00d4d5;
  height: 100%;
  width: 100%;
  position: relative;
  color: #00d4d5;
}

.music-box {
  background: #5318f3;
  height: 100%;
  padding-top: 20px;
  box-sizing: border-box;
  padding-right: 0px !important;
  padding-left: 0px !important;
  position: relative;
  border-left: 5px #00d4d5 solid;
  border-top: 5px #00d4d5 solid;
}

.border {
  width: 3px;
  background: #00d4d5;
  height: 100%;
  margin: 0;
}

h5 {
  font-size: 20px;
  max-width: 300px;
}

.list-box {
  height: 100%;
  background: #5318f3;
  display: flex;
  padding-right: 0px !important;
  padding-left: 0px !important;
  margin-left: 0px !important;
}

.list {
  overflow-y: scroll;
  width: 100%;
  height: 100%;
}

.choice {
  min-width: 100%;
  z-index: 2;
  font-size: 15px !important;
}

.title {
  background: #00d4d5;
  color: #5318f3;
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 25px;
  border-right: 2px #047779 solid;
  border-bottom: 2px #047779 solid;
  border-top: 2px #fbfeff solid;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.songs {
  background: #5318f3;
  margin: 0;
  padding-top: 7px;
  padding-bottom: 7px;
}

.songs:hover {
  background: #00d4d5;
  color: #5318f3;
}

.chosen {
  background: #00d4d5;
  color: #5318f3;
  border-right: 2px #047779 solid;
  border-bottom: 2px #047779 solid;
  border-top: 2px #fbfeff solid;
  border-left: 2px #fbfeff solid;
}

.info {
  height: 80%;
  width: 100%;
}

.navigator {
  background: #00d4d5;
  color: #5318f3;
  height: 20%;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 25px;
  border-bottom: 2px #047779 solid;
  border-top: 2px #fbfeff solid;
  bottom: 0;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.left-nav-bar {
  display: flex;
  width: 25%;
  height: 100%;
  align-items: center;
}

.right-nav-bar {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
}

.hidden {
  display: none !important;
}

.right-nav-bar {
  display: flex;
  align-items: center;
}

.bar {
  border: none;
  border-top: 2px dashed #5318f3;
  color: #fff;
  width: 100%;;
  display: flex;
}

.progress-nav-bar {
  width: 100%;
  display: flex;
  align-items: center;
  height: 100%;
  margin-right: 10px;
  padding-bottom: 10px;
  position: relative;
}

.break {
  position: absolute;
  bottom: calc(50% - 26px);;
  left: -5%;
}

</style>