<template>
  <div class="container">
    <div class="wave-container">
      <div class="wave"></div>
      <div class="wave"></div>
      <div class="wave"></div>
    </div>
    <div class="sea">
      <div class="thefish" id="thefish" style="display: flex">
        <div id="fin">  </div>
        <div id="fishs" class="fish"></div>
        <div id="head"> </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Fishtank",
  mounted() {
    let fin = document.getElementById('fin')
    fin.style.setProperty('transform','rotateY(-30deg)')
    let left = true
    setInterval(() => {
      left = !left
      if (!left) {
        fin.style.setProperty('transform','rotateY(-60deg)')
        return
      }
      fin.style.setProperty('transform','rotateY(60deg)')
      return;
    },2000)

    let fish = document.getElementById('thefish')
    let rotage = false
    let forwardX = true
    let forwardY = true
    let deg = 0
    let x = 0
    let y = 100

    setInterval(()=> {
      if (rotage) {
        let temp = Math.floor(Math.random() * 90)
        if (forwardX) {
          temp += 90
        }
        let olddeg = deg
        deg = forwardY?temp:-temp
        let rot = 0
        rot = deg - olddeg
        fish.style.setProperty('transform',`rotateZ(${rot}deg)`)
        rotage = !rotage
      }
      if (forwardX) {
        x = x+10>100?100:x+10
        if (x == 100) {
          forwardX = false
          fish.style.setProperty('transform',`rotateY(180deg)`)
          rotage = true
        }
      } else {
        x = x-10<0?0:x-10
        if (x == 0) {
          forwardX = true
          fish.style.setProperty('transform',`rotateY(180deg)`)
          rotage = true
        }
      }

      if (forwardX) {
        y = Math.abs(Math.tan(deg))*(100-x)
      } else {
        y = Math.abs(Math.tan(deg))*x
      }

      if (y > 100) {
        y = 100
        forwardY = false
        rotage = true
        return
      }

      if (y < 0) {
        forwardY = true
        rotage = true
        return
      }
      fish.style.setProperty('left',`${x}%`)
      fish.style.setProperty('top',`${y}%`)
    },2000)
  }
}
</script>

<style scoped>
  #fin {
    height: 20px;
    width: 0px;
    border-left: 18px solid #d01f38;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    box-sizing: content-box;
    transform-origin: 100% 50%;
    transition: ease-out 2s;
  }

  .fish {
    color: white;
    font-weight: lighter;
    width: 50px;
    height: 20px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #d01f38;
  }

  #head {
    height: 30px;
    height: 30px;
    color: #d01f38;
    border-radius:50%;
  }

  .thefish {
    height: 50px;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    transition: all linear 2s;
  }

  .container {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  .wave-container {
    height: 20%;
    position: relative;
    overflow: hidden;
  }
  .sea {
    background: linear-gradient(rgba(93, 155, 160, 0.6), #184855) ;
    height: 80%;
    width: 100%;
    z-index: 2;
    position: relative;
  }

  .wave {
    bottom: -30px;
    background: url(https://cdn.kcak11.com/codepen_assets/wave_animation/wave.svg) repeat-x;
    position: absolute;
    width: 20000px;
    height: 70px;
    animation: wave 20s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
    transform: translate3d(0, 0, 0);
  }

  .wave:nth-of-type(2) {
    bottom: -30px;
    left:500px;
    animation: wave 20s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite, swell 7s ease -1.25s infinite;
    opacity: 1;
  }

  .wave:nth-of-type(3) {
    bottom: -30px;
    left:-300px;
    animation: wave 20s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite, swell 7s ease -1.25s infinite;
    opacity: 1;
  }

  @keyframes wave {
    0% {
      margin-left: 0;
    }
    100% {
      margin-left: -1000%;
    }
  }
  @keyframes swell {
    0%,
    100% {
      transform: translate3d(0, -25px, 0);
    }
    50% {
      transform: translate3d(0, 5px, 0);
    }
  }
  .endWave{
    display:none;
  }
</style>
