<template>
  <div>
    <div class="window" :style="{height : height + 'px',width:width +  'px',top: y + 'px',left:x + 'px'}">
      <div class="helper">
      <div class="resize-side">
      </div>
      <div class="content-holder">
        <div class="draggable-top-bar">
          <div draggable="true" class="title" @dragstart="dragStartWindow" @drag="dragWindow" @dragend.prevent="dragOverWindow" >
            <h1>
              {{ this.title }}
            </h1>
          </div>
            <div  class="control-panel">
              <div class="title-box minimize-window" @click.prevent="actionMinimumWindow">
                _
              </div>
              <div class="title-box" @click.prevent="actionCloseWindow">
                X
              </div>
            </div>
        </div>
        <div class="content">
          <slot></slot>
        </div>
      </div>
      <div class="resize-side">
        <div draggable="true" class="corner bottom-right" @dragstart="resizeStartWindow" @drag="resizeWindow" @dragend="resizeOverWindow" ></div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "WindowHolder",
  props: {
    title: String,
    limitX: Number,
    limitY: Number,
    minimumHeight: Number,
    minimunWidth: Number,
  },
  data() {
    return {
      height: 200,
      width: 200,
      x: 200,
      y: 200,
      offsetX: 0,
      offsetY: 0,
      draggable: false,
      resizable: false,
      prevWidth: 0,
      prevHeight: 0,
      prevX : 0,
      prevY : 0,
      zIndex: 0,
    }
  },
  methods: {
    actionMinimumWindow() {
      console.log("minimize Window")
    },
    actionCloseWindow() {
      console.log("Close Window")
    },
    resizeStartWindow(e) {
      if (!this.draggable) {
        this.prevX = e.clientX;
        this.prevY = e.clientY;
        this.prevWidth = this.width
        this.prevHeight = this.height
        this.resizable = true
      }
    },
    resizeWindow(e) {
      if (!this.resizable) return;
      let width = this.prevWidth + (e.clientX - this.prevX);
      if (width < this.minimunWidth) {
        width = this.minimunWidth
      }
      let height = this.prevHeight + (e.clientY - this.prevY);
      if (height < this.minimumHeight) {
        height = this.minimumHeight
      }

      this.width = width
      this.height = height
    },
    resizeOverWindow(e) {
      if (this.resizable) {
        let width = this.prevWidth + (e.clientX - this.prevX);
        if (width < this.minimunWidth) {
          width = this.minimunWidth
        }

        let height = this.prevHeight + (e.clientY - this.prevY);
        if (height < this.minimumHeight) {
          height = this.minimumHeight
        }

        this.width = width
        this.height = height
        this.resizable = false
      }
    },
    dragStartWindow(e) {
      if (!this.resizable) {
        this.draggable = true
        this.offsetX = e.clientX - this.x;
        this.offsetY = e.clientY - this.y;
      }
    },
    dragWindow(e) {
      if (this.draggable) {
        if(e.clientX == 0 && e.clientY ==0) {
          return
        }
        this.x = e.clientX - this.offsetX;
        this.y = e.clientY - this.offsetY;

        if (this.x > this.limitX - this.width) {
          this.x = this.limitX - this.width
        }

        if (this.y > this.limitY - this.height) {
          this.y = this.limitY - this.height
        }

        if (this.x < 0 ) {
          this.x = 0
        }

        if (this.y < 0 ) {
          this.y = 0
        }
      }
    },
    dragOverWindow(e) {
      if (this.draggable) {
          this.x = e.clientX - this.offsetX;
          this.y = e.clientY - this.offsetY;

        if (this.x > this.limitX - this.width) {
          this.x = this.limitX - this.width
        }

        if (this.y > this.limitY - this.height) {
          this.y = this.limitY - this.height
        }

        if (this.x < 0 ) {
          this.x = 0
        }

        if (this.y < 0 ) {
          this.y = 0
        }
        this.draggable = false
      }
    },
  },
  mounted() {
    this.height = this.minimumHeight
    this.width = this.minimunWidth
  }
}
</script>

<style scoped>
.draggable-top-bar {
  background: #00d7cd;
  height: 30px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.title {
  background: #00d7cd;
  width: 100%;
  height: 100%;
  cursor: grab;
  display: flex;
  align-items: center;
  font-size: 14px;
  z-index: 1;
}

.control-panel {
  display: flex;
}

.title-box {
  height: 20px;
  width: 20px;
  margin-left: 4px;
  background: #bfebea;
  border: #047779 3px solid;
  border-top: #ddfbfb 3px solid;
  border-left: #ddfbfb 3px solid;
  font-weight: lighter;
  display: flex;
  font-size: 25px !important;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
  cursor: url("data:image/x-icon;base64,"),auto;
}

.title h1 {
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: 0px;
  font-weight: 400;
  font-size: 24px !important;
  color: #771acd;
  letter-spacing: -3px;
  z-index: -1;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}


.holder {
  width: 100%;
  height: 100%;
}

.helper {
  display: flex;
  justify-content: center;
  align-items: center;
  border: #00d7cd 10px solid;
  border-top: 0px;
  width: 100%;
  height: 100%;
}

.window {
  background: #5318f3;
  position: absolute;
  border: #047779 5px solid;
  border-top: #ddfbfb 5px solid;
  border-left: #ddfbfb 5px solid;
}


.bar {
  width: 100%;
  height: 90%;
  background: blue;
}

.corner {
  height: 20px;
  width: 20px;
  background: transparent;
}

.top-left {
  position: absolute;
  left: -10px;
  top: -10px;
}

.top-left:hover {
  cursor: nw-resize;
}

.top-right {
  position: absolute;
  right: -10px;
  top: -10px;
}

.top-right:hover {
  cursor: ne-resize;
}

.bottom-left {
  position: absolute;
  left: -10px;
  bottom: -10px;
}

.bottom-left:hover {
  cursor: sw-resize;
}

.bottom-right {
  position: absolute;
  right: -10px;
  bottom: -10px;
}

.bottom-right:hover {
  cursor: se-resize;
}

.content-holder {
  height: 100%;
  width: 100%;
}

.resize-side {
  width: 0px;
  height: 100%;
}

.top-bar, .bottom-bar {
  height: 5px;
  width: 100%;
  background: blue;
}

.content {
  height: calc(100% - 25px);
  width: 100%;
}

</style>
